.scene-nav-info {
    bottom: 5px;
    width: 100%;
    text-align: center;
    color: slategrey;
    opacity: 0.7;
    font-size: 10px;
}

.scene-tooltip {
    top: 0;
    color: lavender;
    font-size: 15px;
}

.scene-nav-info, .scene-tooltip {
    position: absolute;
    font-family: sans-serif;
    pointer-events: none;
    user-select: none;
}

.scene-container canvas:focus {
    outline: none;
}
